<template>
  <div class="product-form">
    <form
      class="product-form__form animate-on-load product-form--non-transactional"
      @submit.prevent="handleAddToCart"
    >
      <product-form-header
        block-class="mobile-hide"
        :liquid="liquid"
        :product="product"
        :variant="variant"
        :missing-variant="missingVariant"
        :reviews="reviews"
      />
      <div class="form-group">
        <fieldset
          v-show="!hasOnlyDefaultVariant && selectedOptions.length > 0"
          class="form-group__field"
        >
          <template
            v-for="(option, index) in product.options"
            :key="`variant-${index}`"
          >
            <legend
              class="form-group__title subtitle-1"
              v-html="variantsLabel(option.name)"
            />
            <template v-if="isColorSwatch(option.name)">
              <div
                v-for="(value, i) in option.values"
                :key="`option${index}-${i}`"
              >
                <input
                  :id="`swatch-${i}`"
                  type="radio"
                  name="variant"
                  :value="value"
                  class="product-form__input"
                  @click="updateSelectedVariant(option.name, value)"
                >
                <label
                  :for="`swatch-${i}`"
                  class="product-form__label"
                  :class="{
                    'product-form__colorSwatch': isColorSwatch(option.name),
                    'active': hasVariantId(option.name, value),
                    'out-of-stock': false
                  }"
                >
                  <span
                    class="product-form__text subtitle-2"
                    v-text="value"
                  />
                  <img
                    :src="swatchUrl(value)"
                    :alt="value"
                    class="product-form__swatchImage"
                  >
                </label>
              </div>
            </template>
            <template v-else>
              <div class="product-form__option">
                <select
                  :id="`OptionSelector-${index}`"
                  @change="updateSelectedVariant(option.name, $event.target.value)"
                >
                  <option
                    v-for="(value, i) in option.values"
                    :key="i"
                    :selected="matchingOption(option, value)"
                    v-text="value"
                  />
                </select>
                <icon-caret-down />
              </div>
            </template>
          </template>
        </fieldset>

        <div class="form-group__footer">
          <btn
            class="template-product__add-to-cart main-product__add-to-cart"
            :class="[isAvailable ? 'button--primary' : 'button--disabled']"
            block-class="main-product"
            :label="addToCartLabel"
            modifiers="large block"
            type="submit"
          />
        </div>

        <div
          v-if="hasLowInventory"
          class="product-form__inventory-message"
        >
          <span
            v-html="$string('product.form.low_inventory_message_html')"
          />
          <tooltip :message="lowInventoryMessage" />
        </div>

        <div
          v-if="liquid.shippingMessage && liquid.enableShippingMessage"
          class="product-form__shipping"
        >
          <icon-error />
          {{ liquid.shippingMessage }}
        </div>

        <div
          v-if="variant.sku"
          class="product-form__sku"
        >
          SKU: <span v-text="variant.sku" />
        </div>

        <!-- Twistering products (family bundles) -->
        <div
          v-if="family.length"
          class="form-group__field product-form__family-bundle"
        >
          <fieldset class="product-form__family-bundle-items">
            <legend class="product-form__family-bundle-title h5"
              v-text="$string('product.form.save_with_sets')"
            />
            <template
              v-for="member in family"
              :key="`model-${member.id}`"
            >
              <button
                class="product-form__family-bundle-card"
                type="button"
                tabindex="0"
                @click="selectMember(member)"
              >
                <span
                  v-if="getMemberBadge(member)"
                  class="product-form__family-bundle-badge badge"
                  v-text="getMemberBadge(member)"
                />

                <div class="product-form__family-bundle-card-image">
                  <responsive-image
                    class="responsive-card-image__wrapper product-form__family-bundle-image"
                    ratio="1:1"
                    :padding="true"
                    :image="getMemberImage(member)"
                    :lazyload="true"
                    force-update
                  />
                </div>

                <div class="product-form__family-bundle-card-label">
                  <span class="product-form__family-bundle-card-title subtitle-1"
                    v-text="member.title"
                  />

                  <product-price-message
                    v-if="hasPricingMessage"
                    :lowest-price="lowestPrice(member)"
                    :recommended-price="recommendedPrice(member)"
                  />

                  <span class="product-form__family-bundle-card-price  body-1">
                    <product-prices
                      :class="$blockClass(blockClass, 'family-bundle-prices')"
                      :available="product?.available"
                      block-class="family-bundle"
                      :compare-at-price="member.compareAtPrice.amount"
                      :price="member.price.amount"
                      disabled-pricing-directive
                      small-text
                    />
                  </span>
                </div>
              </button>
            </template>
          </fieldset>
        </div>

        <PostalCodeValidator v-if="showPostalCodeValidator" />
      </div>
    </form>

    <p
      v-if="error"
      class="main-product__error text-body-m"
      v-text="error"
    />

    <p
      v-if="isPreOrder"
      class="main-product__message text-body-m"
    >
      <icon-error />
      <span
        v-text="$string(
          'product.form.pre_order_message',
          { replace: { date: formatDate(product.preOrder) } }
        )"
      />
    </p>
  </div>
  <product-bundles
    :product="product"
    @add-bundle="addProductBundle"
  />
</template>

<script>

/**
 * Vue: Product form (product-form)
 * -----------------------------------------------------------------------------
 * Product form to handle variant selection and adding to cart.
 *
 * @param {Boolean} liquid.hasOnlyDefaultVariant - Has only default variant.
 * @param {Object|Boolean} product - Canvas formatted product object.
 * @param {Object|Boolean} variant - Canvas formatted variant object.
 *
 */
import { nextTick } from 'vue'
import { mapActions, mapState } from 'vuex'

import ProductFormHeader from '~async/product-form/product-form-header'
import Btn from '~global/btn/btn'
import Tooltip from '~global/tooltip/tooltip'
import PostalCodeValidator from '~global/postal-code-validator/postal-code-validator'
import ProductBundles from '~global/product-bundles/product-bundles.vue'
import ProductPriceMessage from '~global/product-price-message/product-price-message'
import IconCaretDown from '~icons/direction/caret/down.svg'
import IconError from '~icons/misc/error.svg'
import timings from '~/config/timings'

import productsByQuery from '~/graphql/queries/productsByQuery.gql'
import { addToCartEvent } from '~/helpers/datalayer'
import { formatProduct } from '~/helpers/format'
import { handleize } from '~/helpers/general'


export default {
  name: 'ProductForm',

  components: {
    ProductFormHeader,
    ProductPriceMessage,
    Btn,
    IconError,
    IconCaretDown,
    ProductBundles,
    Tooltip,
    PostalCodeValidator,
  },

  props: {
    liquid: {
      type: Object,
      default: () => ({
        hasOnlyDefaultVariant: false,
      }),
    },
    product: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
    variant: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
    reviews: {
      type: String,
      default: () => (''),
    },
  },

  emits: [
  'update-variant', 'update-product', 'variant-check',
  ],

  data() {
    return {
      currentVariantId: this.variant?.id || 0,
      error: '',
      lineItemProperties: {},
      quantity: 1,
      state: 'ready',
      timeout: {},
      family: [],
      missingVariant: false,
      selectedBundle: [],
      selectedOptions: this.variant?.selectedOptions || [],
      quantityLimit: parseInt(cnvs.settings.quantityLimits.storeLimit),
      quantityLimitSettings: cnvs.settings.quantityLimits.storeLimit && cnvs.settings.quantityLimits.enable,
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      cart: (state) => state.cart.response,
      isMobile: (state) => state.index.screen.mobile,
    }),

    /**
     * Compute add to cart text.
     * @returns {String}
     */
     addToCartText() {
      if (this.isComingSoon) {
        return this.$string('product.cta.coming_soon')
      } else if (this.isPreOrder) {
        return this.$string('product.form.pre_order')
      } else if (!this.isAvailable) {
        return this.$string('product.cta.out_of_stock')
      }

      if (this.state === 'adding') {
        return this.$string('product.cta.adding_to_cart')
      }

      if (this.state === 'limitreached') {
        return this.$string('product.cta.add_to_cart_limit')
      }

      return this.state === 'added'
          ? this.$string('product.cta.added_to_cart')
          : this.$string('product.cta.add_to_cart')
    },

    /**
     * Compute the count of selected items for adding to cart
     * @returns {Number}
     */
     selectedItemCount() {
      let totalItems = 1
      if (this.selectedBundle && this.selectedBundle.length > 0) {
        const uniqueBundle = this.getUniqueItemsById(this.selectedBundle)
        uniqueBundle.forEach((item) => {
          totalItems += item.quantity || 1
        })
      }
      return totalItems
    },

    /**
     * to add a label for item count
     */
    addToCartLabel() {
    const countText = this.selectedItemCount > 1 ? ` (${this.selectedItemCount})` : ''
    return `${this.addToCartText}${countText}`
  },

    /**
     * Compute has only default variant from Liquid and product objects.
     * @returns {Boolean}
     */
    hasOnlyDefaultVariant() {
      return this.liquid.hasOnlyDefaultVariant || this.product.hasOnlyDefaultVariant
    },

    hasLowInventory() {
      const currentVariant = this.product.variants.find((variant) => variant.id == this.currentVariantId)
      const inventoryThreshold = cnvs.settings.lowInventory.threshold
      const hasLowInventory = currentVariant.inventory <= inventoryThreshold && currentVariant.inventory > 0
      return cnvs.settings.lowInventory.enable && hasLowInventory
    },

    hasPricingMessage() {
      const enabled = this.$variable('settings.pricingDirective.enable')

      if (!enabled) {
        return false
      }

      return enabled
    },

    /**
     * Compute model label
     */
    modelLabel() {
      return `${this.$string('product.form.model')}: `
    },

    /**
     * Get low inventory message
     */
    lowInventoryMessage() {
      return  `${this.$string('product.form.low_inventory_tooltip_html')}`
    },

    /**
     * Compute active variant availability
     */
    isAvailable() {
      return this.variant.available
    },

    /**
     * Compute whether to display preOrder messaging.
     */
    isPreOrder() {
      return this.liquid.allowPreOrder && this.product.preOrder
    },

    /**
     * Compute whether to display coming soon messaging.
     */
     isComingSoon() {
      if (!this.product?.tags || !this.variant?.sku) {
        return false
      }

      if (this.variant.inventory > 0) {
        return false
      }

      return this.product.tags.includes(`coming-soon:${this.variant.sku}`)
    },

    /**
     * Whether or not to show the postal code component
     * @returns {Boolean}
     */
    showPostalCodeValidator() {
      return cnvs.settings.showPostalCodeValidatorOnPdp
    },

    /**
     * Compute if product has family tag
     * @returns {Boolean}
     */
    familyTag() {
      const family = this.getTag('product-family:')
      if (!family) {
        return family
      }

      return family.replace('product-family:', '')
    }
  },

  watch: {

    /**
     * Watch currentVariantId to update Canvas variant when select changes.
     */
    currentVariantId() {
      if (this.variant.id === this.currentVariantId) {
        return
      }

      const variant = this.getVariantFromId(this.currentVariantId)

      if (!variant) {
        return
      }

      this.$emit('update-variant', variant)
    },

    /**
     * Watch product to update selected variant when product data is loaded.
     */
    product() {
      this.getCurrentVariant()
      this.queryFamilyProducts()
    },
  },

  mounted() {
    this.getCurrentVariant()
    this.getSelectedOptions()
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      addItem: 'cart/addItem',
      openOverlay: 'overlays/open',
    }),

    /**
     * Get unique bundles from the list of bundles
     */
    getUniqueItemsById(items) {
      const uniqueItemsMap = new Map()

      items.forEach(item => {
        if (!uniqueItemsMap.has(item.id)) {
          uniqueItemsMap.set(item.id, item)
        }
      })

      return Array.from(uniqueItemsMap.values())
    },

    /**
     * @param {Object} item - product family member
     * @returns {String|Boolean}
     */
    getMemberBadge(item) {
      const { compareAtPrice, price } = item
      if (!compareAtPrice.amount || compareAtPrice.amount <= price.amount) {
        return false
      }

      const discountPercentage = 100 - Math.floor(((price.amount / compareAtPrice.amount) * 100));
      if (discountPercentage <= 0) {
        return false
      }

      const saleText = this.$string('layout.general.sale').replace('{{amount}}', discountPercentage)
      return saleText
    },

    /**
     * Get product lowest price.
     * @param {Object} item - product family member
     * @returns {Number}
     */
    lowestPrice(item) {
      const variant = this.colorMatchVariant(item)

      if (!variant?.lowestPrice) {
        return false
      }

      const sanitizedLowestPrice = variant.lowestPrice
        .replace(/_DELETE_/g, '')

      return sanitizedLowestPrice.length > 0
        ? sanitizedLowestPrice
        : false
    },

    /**
     * Get product recommended price.
     * @param {Object} item - product family member
     * @returns {Number}
     */
    recommendedPrice(item) {
      const variant = this.colorMatchVariant(item)
      return variant.variantComparePrice > variant.variantPrice
        ? this.$formatMoney(variant.variantComparePrice)
        : false
    },

    /**
     * Get product recommended price.
     * @param {Object} item - product family member
     * @returns {Object}
     */
    getMemberImage(item) {
      const variant = this.colorMatchVariant(item)
      const image = item.images.find((image) => {
        return variant.image === image.id
      })

      if (image) {
        return image
      }

      return item.featuredImage
    },

    /**
     * Get variant that matches currently selected PDP variant colour.
     * - returns the first variant by default
     * @param {Object} item - product family member
     * @returns {Object} - product variant
     */
    colorMatchVariant(item) {
      const selectedColor = this.variant.selectedOptions.find((variant) => {
        return variant.name.toLowerCase() === 'color'
      })

      if (!selectedColor) {
        return item.variants[0]
      }

      const colorVariant = item.variants.find((variant) => {
        const colorValue = variant.selectedOptions.find((option) => {
          return option.name.toLowerCase() === 'color'
        })
        return selectedColor.value.toLowerCase() === colorValue.value.toLowerCase()
      })

      if (!colorVariant) {
        return item.variants[0]
      }

      return colorVariant
    },

    /**
     * Get current variant on page load or product update.
     */
    getCurrentVariant() {
      if (
        !this.product ||
        !this.product.variants.length
      ) {
        return
      }

      if (this.hasOnlyDefaultVariant) {
        this.currentVariantId = this.product.variants[0].id
        return
      }

      /**
       * Pick the first in stock variant.
       * - If no variants are in stock use first.
       * - Or use variant from URL if set.
       */
      const inStockVariants = this.product.variants.filter((variant) => {
        return variant.available
      })

      const defaultVariant = inStockVariants.length
        ? inStockVariants[0]
        : this.product.variants[0]

      const variant = this.getVariantFromUrl() || defaultVariant

      this.currentVariantId = variant.id
    },

    /**
     * Get initial variant from URL parameter.
     * @returns {Object}
     */
    getVariantFromUrl() {
      const params = new URLSearchParams(document.location.search.substring(1))
      const variantId = Number(params.get('variant'))
      return this.getVariantFromId(variantId)
    },

    /**
     * Get variant object from variant ID.
     * @returns {Object}
     */
    getVariantFromId(variantId) {
      return this.product.variants.find((variant) => {
        return variant.id === variantId
      })
    },

    /**
     * Get variant title.
     * @param {Object} variant - Variant object.
     * @returns {String}
     */
    getVariantTitle(variant) {
      return variant.available
        ? variant.title
        : `${variant.title} - ${this.$string('product.form.sold_out')}`
    },

    /**
     * Handle quantity selector update.
     * @param {String} quantity - New quantity.
     */
    handleQuantityUpdate(quantity) {
      this.quantity = quantity
    },

    /**
     * Handle add to cart submission.
     */
    async handleAddToCart() {
      if (!this.variant) {
        return
      }

      clearTimeout(this.timeout)
      this.error = ''
      this.state = 'adding'

      if (cnvs.storybook) {
        this.timeout = setTimeout(() => {
          this.state = 'added'
        }, timings.slow)

        this.timeout = setTimeout(() => {
          this.state = 'ready'
        }, timings.message)

        return
      }

      this.getEcoTax()

      this.lineItemProperties._lowestPrice = this.variant.lowestPrice
      this.lineItemProperties._recommendedPrice = this.variant.recommendedPrice

      if (this.familyTag) {
        this.lineItemProperties._familyBundle = this.familyTag
      }

      const items = [
        {
          id: this.variant.id,
          properties: this.lineItemProperties,
          quantity: this.quantity,
        },
      ]

      if(this.selectedBundle && this.selectedBundle.length > 0) {
        const uniqueBundle = this.getUniqueItemsById(this.selectedBundle)
        uniqueBundle.forEach((item) => {
          items.push(item)
        })
      }
      const currentVariantInCart = this.cart.items.filter((item) => item.id === this.variant.id)
      const currentVariantQuantity=  currentVariantInCart.reduce((acc, current) => acc + current.quantity,0)
      if ((currentVariantQuantity === this.quantityLimit && this.quantityLimitSettings) || (currentVariantQuantity > this.quantityLimit && this.quantityLimitSettings)) {
        this.state = 'limitreached'
        return
      }

      if (!this.quantityLimitSettings || currentVariantQuantity < this.quantityLimit) {
        try {
          await this.addItem(items)
          this.openOverlay({
            component: 'cart-drawer',
            ignoreDismissed: true,
            namespace: 'cartDrawer',
          })

          this.state = 'added'

          this.timeout = setTimeout(() => {
            this.state = 'ready'
          }, timings.message)

          this.addDatalayer()

        } catch (error) {
          this.state = 'error'

          if (error.length) {
            this.error = error
            return
          }
          reportError('Failed to add product to cart', error)
        }
      }
    },

    /**
     * Read product tag
     * @param {String} key
     * @param {Object} product
     */
    getTag(key, product = null) {
      const data = product ? product : this.product

      const tag = data.tags.find((tag) => {
        return tag.includes(key)
      })

      if (!tag) {
        return false
      }

      return tag
    },

    /**
     * Load products by tag from GraphQL.
     */
    async queryFamilyProducts() {
      const family = this.getTag('product-family:')

      if (!family) {
        return
      }

      const query = `tag:'${family}'`

      try {
        const response = await cnvs.Query({
          locale: cnvs.store.language,
          query: productsByQuery,
          variables: {
            country: cnvs.store.country,
            query,
          },
        })

        if (
          cnvs.settings.disableComponentReadyState ||
          !response.products.edges.length
        ) {
          return
        }

        const filterProducts = response.products.edges.filter((product) => {
          return this.product.handle !== product.node.handle
        })

        this.family = filterProducts.map((node) => {
          return formatProduct({ product: Object.values(node)[0] })
        })
        console.log('this.family---->', this.family)

      } catch (error) {
        reportError('Failed to load family products', error)
      }
    },

    /**
     * Get model name from product tag
     * @param {Object} product
     */
    getModelName(product) {
      const model = this.getTag('model:', product)

      if (!model) {
        return
      }

      return model.split(':').splice(-1)[0]
    },

    /**
     * Get handleized model.
     */
    getModelHandle(product) {
      const model = this.getModelName(product)

      return handleize(model)
    },

    /**
     * Compare product id to active product
     * @param {Object} product
     */
    matchesParent(product) {
      return product.id === this.product.id
    },

    /**
     * Emit selected family product member.
     * @param {Object} member
     */
    selectMember(member) {
      this.$emit('update-product', member)
      const variantAvailable = member.variants.find((bundleVariant) => {
        return bundleVariant.available === true &&
          this.variant.title.toLowerCase() === bundleVariant.title.toLowerCase()
      })

      if (variantAvailable) {
        nextTick(() => {
          this.currentVariantId = variantAvailable.id
          return
        })
      }
    },

    /**
     * Get Variant name from ID.
     * @param {Number} id
     */
    getVariantName(id) {
      if ( !this.product?.variants?.length ) {
        return
      }

      const variant = this.product.variants.find((variant) => {
        return variant.id === id
      })

      if(!variant) {
        return
      }

      return variant.title
    },

    /**
     * Get eco tax message.
     */
    getEcoTax() {
      const ecoTaxMessage = this.$string('product.form.eco_tax')

      if (!this.variant?.ecoTax || this.variant?.ecoTax === '') {
        return
      }

      this.lineItemProperties.ecoTax = ecoTaxMessage.replace( '{{amount}}', this.variant.ecoTax)
    },

    /**
     * Determine if variant is a color swatch.
     * @param {Object} variant
     */
    isColorSwatch(option) {
      const optionName = option.toLowerCase()
      return optionName === 'color' || optionName === 'colour'
    },

    /**
     * Get swatch asset URL
     * @param {String} name
     */
    swatchUrl(name) {
      const color = handleize(name)
      return cnvs.urls.files
          .replace('placeholder', `swatch_${color}_40x40.png`)
    },

    /**
     * Format date string to MM/DD/YYYY
     */
    formatDate(date) {
      const data = date.split('-')
      return `${data[1]}/${data[2]}/${data[0]}`
    },

    addDatalayer() {
      const productData = { ...this.variant, type: this.product.type }
      const additionalData = {
        newCart: this.cart.items.quantity < 1,
       }

      addToCartEvent(productData, additionalData)
    },

    addProductBundle(item) {
    const existingItemIndex = this.selectedBundle.findIndex(bundleItem => bundleItem.id === item.id)
    if (item.checked) {
      if (existingItemIndex === -1) {
        this.selectedBundle.push(item)
      }
    } else {
      if (existingItemIndex !== -1) {
        this.selectedBundle.splice(existingItemIndex, 1)
      }
    }
  },

    variantsLabel(option = '') {
      const color = this.isColorSwatch(option)
      const selectedColor = this.selectedOptions.find((option) => ['color', 'colour'].includes(option.name.toLowerCase()))
      const label = color
        ? `${this.$string('product.form.variants').replace('{{ option }}', option)}: <span>${selectedColor.value}</span>`
        : option

      return `${label} `
    },

    getMatchingVariant() {
      if(!this.selectedOptions) {
        return
      }

      let other = false
      this.product.variants.forEach((variant) => {
        let match = true

        variant.selectedOptions.forEach((selected) => {
          const matchedOption = this.selectedOptions.find((selectedOption) => selectedOption.name === selected.name && selectedOption.value === selected.value)

          if (match) {
            match = matchedOption
          }
        })

        if (match) {
          other = variant
        }
      })

      return other || null
    },

    hasVariantId(option, value) {
      const selectedVariant = this.product.variants.filter((variant) => {
        return variant.selectedOptions.find((selected) => selected.name === option && selected.value === value )
      })

      if (!selectedVariant || !this.currentVariantId) {
        return
      }

      const matchingID = selectedVariant.find(variant => variant.id === this.currentVariantId)

      return matchingID
    },

    isMatchingVariant() {
      const selectedVariant = this.getMatchingVariant()
      if (!selectedVariant) {
        this.missingVariant = true
        if (this.isMobile) { this.$emit('variant-check', this.missingVariant)}
        return false
      }

      this.currentVariantId = selectedVariant.id
      this.missingVariant = false

      if (this.isMobile) {
        this.$emit('variant-check', this.missingVariant)
      }

      return this.currentVariantId === selectedVariant.id
    },

    matchingOption(option, value) {
      const match = this.selectedOptions.find((selection) => selection.name === option.name)
      return match ? match.value === value : false
    },

    variantOptionAvailable() {
      return true
    },

    getSelectedOptions() {
      if(!this.variant) {
        return
      }

      this.selectedOptions = this.variant.selectedOptions
    },

    updateSelectedVariant(option, value) {
      Object.keys(this.selectedOptions).forEach(index => {
        if(this.selectedOptions[index].name === option) {
          this.selectedOptions[index].value = value
        }
      })

      this.isMatchingVariant()
    },
  },
}
</script>

